<template>
  <div>
    <div id="leixin">
      <!-- <h3 class="title_container">调整成本单价</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" @focus="companyListInvok" @change="companyChange" placeholder="请选择" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.adjust_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status" clearable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核时间" size="mini">
            <el-date-picker v-model="timeValue2" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="AddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="examineFun(2)" icon="iconfont icon-pass" type="primary" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="examineFun(3)" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" max-height="600" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="AddF(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="adjust_sn" width="160" label="单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="120" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="170" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="130" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="80" label="审核状态" text-align="center">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="120" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import {
  getData,
  publicFn,
  opthionList,
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageAdjustcostAdjustcostIndex",
  data() {
    return {
      timeValue2: [],
      loading:false,
      dialogFormVisible: false,
    };
  },
  computed: {
    ...mapGetters(["adjustcostIndexGet"]),
  },
  methods: {
    ...mapActions([
      "adjustcostAddACT",
      "adjustcostSeeACT",
      "adjustcostModifyACT",
      "adjustcostIndexACT",
    ]),
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
    },
    seeDetail(scope, row) {
      this.$router.push({
        path: "/inventory/adjustcost/adjustcostSee",
      });
      this.adjustcostSeeACT({
        adjust_id: row.adjust_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
    },
    AddF(scope, row) {
      this.$router.push({
        path: "/inventory/adjustcost/adjustcostAdd",
      });
      this.adjustcostAddACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
        adjust_id: row ? row.adjust_id : "",
      });
    },
    onReset(formName) {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.timeValue2 = null;
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getAdjustCostList", //接口名
        vm: this, //传递的this
      });
      this.onSearch();
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.aud_start_time = this.timeValue2[0];
        this.params.aud_end_time = this.timeValue2[1];
      } else {
        this.params.aud_start_time = this.params.aud_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      this.adjustcostIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
      });
      getData(this.searchParams);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要操作的信息",
          type: "warning",
        });
        return false;
      }
      var deledata = {
        deleID: "adjust_id",
        deleSelect: this.multipleSelection,
        deleApi: "deladjustGoods",
        list: {
          UrlApi: "getAdjustCostList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.deletes(deledata, "删除", this);
    },
    examineFun(status) {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要操作的信息",
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].adjust_id);
      }
      var adjust_id = addr.toString();
      var funcan = {
        examineID: "adjust_id",
        eachData: {
          adjust_id: adjust_id,
        },
        examineApi:
          status == 2 ? "adjustCostAuidtorPass" : "adjustCostAuditorInvalid",
        list: {
          UrlApi: "getAdjustCostList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.examineFun(funcan);
    },
  },
  mounted() {
    if (this.adjustcostIndexGet && this.adjustcostIndexGet.params) {
      if (this.adjustcostIndexGet.params.start_time) {
        const { start_time, end_time } = this.adjustcostIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
        const { aud_start_time, aud_end_time } = this.adjustcostIndexGet.params;
        this.timeValue2 = this.timeValue2.concat([
          aud_start_time,
          aud_end_time,
        ]);
      }
      this.$set(this, "params", this.adjustcostIndexGet.params);
      opthionList(this, this.adjustcostIndexGet.params);
    }
    if (this.adjustcostIndexGet.goPage) {
      this.params.page = this.adjustcostIndexGet.goPage;
      this.params.per_page = this.adjustcostIndexGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getAdjustCostList", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
  },
};
</script>
<style scoped>
